.form {
  position: relative;
  top: 30%;
  margin-top: 60px;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-transition: all 1s;
  transition: all 1s;
  width: 50px;
  height: 50px;
  background: white;
  box-sizing: border-box;
  border-radius: 25px;
  border: 4px solid white;
  padding: 5px;
}

.form input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  /* height: 42.5px; */
  line-height: 30px;
  outline: 0;
  border: 0;
  display: none;
  font-size: 1em;
  border-radius: 20px;
  padding: 0 20px;
}

.form .fa {
  box-sizing: border-box;
  padding: 10px;
  width: 42.5px;
  height: 42.5px;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
  color: #343a40;
  text-align: center;
  font-size: 1.2em;
  -webkit-transition: all 1s;
  transition: all 1s;
}

.form:hover {
  width: 200px;
  cursor: pointer;
}

.form:hover input {
  display: block;
}

.form:hover .fa {
  background: #343a40;
  color: white;
}

a {
  /* color: steelblue; */
  text-decoration: none;
  -webkit-transition: 0.2s opacity;
  transition: 0.2s opacity;
}
a:hover,
a:focus {
  text-decoration: none;
  outline: none;
  opacity: 0.4;
}

